<template>
  <form @submit.prevent="submitCustomer" class="flex-center" >
    <div class="input-field margin-fix right-margin-little">
      <input
        class="input-add"
        type="tel"
        id="customer"
        v-model.trim="number"
        v-mask="'+7 (###) ###-##-##'"
        placeholder="–ù–æ–º–µ—Ä –Ω–æ–≤–æ–≥–æ –∫–ª–∏–µ–Ω—Ç–∞"
        :class="{invalid: ($v.number.$dirty && !$v.number.required) || ($v.number.$dirty && !$v.number.minLength)}"
      >
      <small
        class="helper-text invalid"
        v-if="$v.number.$dirty && !$v.number.required"
      >
        –í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä –∫–ª–∏–µ–Ω—Ç–∞
      </small>
      <small
        class="helper-text invalid"
        v-else-if="$v.number.$dirty && !$v.number.minLength"
      >
        –ù–æ–º–µ—Ä –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å –Ω–µ –º–µ–Ω–µ–µ {{$v.number.$params.minLength.min}} —Å–∏–º–≤–æ–ª–æ–≤.
      </small>
      <small
        class="helper-text invalid"
        v-else-if="coincidence"
      >
        –ù–æ–º–µ—Ä —É–∂–µ –µ—Å—Ç—å
      </small>
    </div>

    <button class="btn-transparent transparent btn-page-title blue-text text-darken-1" title="–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤–æ–≥–æ –∫–ª–∏–µ–Ω—Ç–∞" type="submit">
      <i class="material-icons big-material-icons">add</i>
    </button>
  </form>
</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators'
import { mask } from 'vue-the-mask'

export default {
  name: 'AddCustomer',
  data () {
    return {
      number: '',
      coincidence: false
    }
  },
  props: ['customers'],
  directives: { mask },
  validations: {
    number: { required, minLength: minLength(7) }
  },
  methods: {
    createCustomer () {
      if (this.number.trim() && !this.coincidence) {
        // Body New Customer
        const newCustomer = {
          id: Date.now(),
          name: '–ù–æ–≤—ã–π –ö–ª–∏–µ–Ω—Ç',
          contractNumber: '',
          contractDate: '',
          number: this.number,
          fax: '',
          site: '',
          email: '',
          address: '',
          note: '–ù–æ–≤—ã–π –∫–ª–∏–µ–Ω—Ç',
          manager: '',
          status: '–î–µ–π—Å—Ç–≤—É—é—â–∏–π'
        }

        console.log('E3')

        this.$emit('add-customer', newCustomer)
        this.number = ''

        // this.$router.push(`/customers/edit/customers${newCustomer.id}`)
      }
    },

    submitCustomer () {
      console.log('E1')
      // For validations
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }

      console.log('E2')

      this.createCustomer()
    }
  },
  mounted () {
  }
}
</script>
